
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner {
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.fadeRotate-enter-active,
.cartcontrol .cart-decrease.fadeRotate-leave-active {
  transform: translate3d(0, 0, 0);
}
.cartcontrol .cart-decrease.fadeRotate-enter-active .inner,
.cartcontrol .cart-decrease.fadeRotate-leave-active .inner {
  display: inline-block;
  transform: rotate(0);
}
.cartcontrol .cart-decrease.fadeRotate-enter,
.cartcontrol .cart-decrease.fadeRotate-leave-active {
  opacity: 0;
  transform: translate3d(24px, 0, 0);
}
.cartcontrol .cart-decrease.fadeRotate-enter .inner,
.cartcontrol .cart-decrease.fadeRotate-leave-active .inner {
  transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  line-height: 24px;
  text-align: center;
  padding: 6px 0;
}
.cartcontrol .cart-add {
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
  color: #00a0dc;
  line-height: 24px;
  padding: 6px;
}


